<template>
  <GMap
    ref="map"
    class="MapBox"
    :center="mapCenter"
    :zoom="14"
    :options="{
      disableDefaultUI: true,
      styles: theme,
    }">
    <GMark
      v-if="!this.kml && this.mark.label"
      :label="mark.label"
      :position="mapCenter"
      :options="{
        label: {
          text: mark.label,
          color: '#0E0E0E',
        },
        icon:{
          url: mark.icon,
          labelOrigin:{ x:90, y:65 },
        },
      }"/>
  </GMap>
</template>

<script>
import {gmapApi as GM} from 'vue2-google-maps'

import GMap  from 'vue2-google-maps/src/components/map'
import GMark from 'vue2-google-maps/src/components/marker'

// import theme from "./theme.json"
let
theme = [
  { featureType: 'poi.business',
    stylers: [{visibility: 'off'}]
    },
  { featureType: 'road',
    elementType: 'labels.text.fill',
    stylers: [
      {color: '#BBBBBB'},
    ]
  },
]

export default {
  name: "MapBox",
  props:[ 'map', 'mark', 'kml' ],
  components:{ GMap, GMark },
  mounted(){
    if( this.kml )
      this.$refs.map.$mapPromise.then(this.setKML)
  },
  data:()=>({ theme }),
  computed:{
    Google: GM,
    gKML(){
      if( !this.kml ) return

      var
      url,
      mid,
      map = this.kml

      if( map.split('?').length == 1 ) 
        mid = map
      else if( map = map.split('?')[1] ){
        mid = map.split('&')
        mid = mid.map(param=> !param.indexOf('id=') ? param.split('=')[1] : false)
        mid = mid.filter(p=>p)[0]
      }

      url = `https://google.com/maps/d/kml?forcekml=1&mid=${mid}&v=${Math.round(new Date().getTime()/1000)}`

      this.$log({url,map,mid})
      return url
    },
    mapCenter(){
      if( !this.map ) return { lat:0, lng:0 }
      let
      lat = parseFloat(this.map.lat||0),
      lng = parseFloat(this.map.lng||0)
      return {lat,lng}
    }
  },
  methods:{
    setKML(map){
      new this.Google.maps.KmlLayer({
        map,
        url: this.gKML
      });
    }
  }
}
</script>

<style lang="scss" scoped>
@import "~@/styles/theme/breaks";
.MapBox {
  .UiBox > &:only-child {
    width: 100vw !important;
    min-height: 32vh;
    position: absolute;
    top: 0;
    left: 0;
    width: 100% !important;
    height: 100% !important;
  }
}
</style>
<style lang="scss">
.MapBoxWrap {
  &.UiBox {
    min-height: 16rem !important;
  }
}
</style>
